
import { defineComponent } from '@vue/composition-api';
import { date } from '../../filters';

interface TypeName {
  [propName: number]: string;
}
interface OrderItem {
  num: string,
  orderedTime: number,
  type: number,
  status: number
}

const TYPE_TO_TYPE_NAME: TypeName = {
  0: '材料扫描',
};
const data: OrderItem[] = [
  {
    num: '2020040200001',
    orderedTime: 1585831558977,
    type: 0,
    status: 0,
  },
  {
    num: '2020040200001',
    orderedTime: 1585831558977,
    type: 0,
    status: 1,
  },
  {
    num: '2020040200001',
    orderedTime: 1585831558977,
    type: 0,
    status: 2,
  },
  {
    num: '2020040200001',
    orderedTime: 1585831558977,
    type: 0,
    status: 4,
  },
  {
    num: '2020040200001',
    orderedTime: 1585831558977,
    type: 0,
    status: 5,
  },
  {
    num: '2020040200001',
    orderedTime: 1585831558977,
    type: 0,
    status: 3,
  },
];

const STATUS = [
  { name: '订单生成' },
  { name: '寄出实物' },
  { name: '开始制作' },
  { name: '已上传' },
  { name: '完成订单' },
  { name: '订单已取消' },
];

const STATUS_IN_STEP = STATUS.slice(0, 5);
const dateFormat = 'DD/MM/YYYY HH:mm';

const Order = defineComponent({
  render() {
    // const self = this as InstanceType<typeof Order>;
    return <div class="card">
      <div style="margin-bottom: 15px;">
        <el-button style="padding-left: .75em; padding-right: .75em" type="primary"><i class="iconfont iconrenwudongtai" /> 下单</el-button>
      </div>
      <el-table border={true} data={data} style="width: 100%;">
        <el-table-column label="序号" type="index" width="60" />
        <el-table-column label="订单号" prop="num" />
        <el-table-column label="下单时间" width="150" {...{
          scopedSlots: {
            default: ({ row: { orderedTime } }: { row: OrderItem }) => {
              return date(new Date(orderedTime), dateFormat);
            },
          },
        }} />
        <el-table-column label="扫描类型" width="120" {...{
          scopedSlots: {
            default: ({ row: { type } }: { row: OrderItem }) => TYPE_TO_TYPE_NAME[type],
          },
        }} />
        <el-table-column label="流程状态" width="320" {...{
          scopedSlots: {
            default: ({ row: { status } }: { row: OrderItem }) => {
              if (status >= STATUS_IN_STEP.length) {
                return STATUS[status].name;
              }
              return <el-steps space={60} active={status}
                finish-status="success" align-center>
                {
                  STATUS_IN_STEP.map(({ name }) => (
                    <el-step title={name} />
                  ))
                }
              </el-steps>;
            },
          },
        }} />
        <el-table-column label="操作" width="380" {...{
          scopedSlots: {
            default: ({ row }: { row: OrderItem}) => {
              const { status } = row;
              return (<div>
                <el-button size="small" disabled={status !== 0} onClick={() => { row.status = 1; }}>确认寄出</el-button>
                <el-button size="small" disabled={status < 3 || status === 5} onClick={() => { row.status = 4; }}> 完成订单</el-button>
                <el-button size="small" disabled={status === 5} onClick={() => { row.status = 5; }}>取消订单</el-button>
                <el-button size="small" disabled={status < 3 || status === 5}> 查看结果</el-button>
              </div>);
            },
          },
        }} />
      </el-table>
    </div>;
  },
});

export default Order;
